import React from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";

const FooterPage = () => {
  const mystyle = {
    bottom: 0,
    left: 0,
    padding: "20px",
    width: "100%",
    backgroundColor: "#343a40",
  };

  return (
    <MDBFooter color="blue" className="footer" style={mystyle}>
      <MDBContainer fluid className="text-center text-md-left">
        <MDBRow>
          <MDBCol md="6">
            <h5 className="title" style={{ color: "white" }}>
              Iqbal Husain
            </h5>
            <p style={{ color: "white" }}>
              Iqbal Husain, in full Pablo Diego José Francisco de Paula Juan
              Nepomuceno Crispín Crispiniano María Remedios de la Santísima
              Trinidad Ruiz Picasso, also called (before 1901) Pablo Ruiz Pablo
              Ruiz Picasso, (born October 25, 1881, Málaga, Spain—died April 8,
              1973, Mougins, France), Spanish expatriate painte, sculptor,
              printmaker, ceramicist, and stage designer, one of the greatest
              and most-influential artists of the 20th century and the creator
              (with Georges Braque) of Cubism. (For more information on
              Picasso’s name see Researcher’s Note: Picasso’s full name.)
            </p>
          </MDBCol>
          <MDBCol md="6" className="text-right">
            <h5 className="title" style={{ color: "white" }}>
              Social Media
            </h5>
            <ul>
              <li className="list-unstyled">
                <a href="#!" style={{ color: "yellow" }}>
                  Facebook
                </a>
              </li>
              <li className="list-unstyled">
                <a href="#!" style={{ color: "yellow" }}>
                  Instagram
                </a>
              </li>
              <li className="list-unstyled">
                <a href="#!" style={{ color: "yellow" }}>
                  Twitter
                </a>
              </li>
            </ul>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
      <div className="footer-copyright text-center py-3">
        <MDBContainer fluid style={{ color: "white" }}>
          &copy; {new Date().getFullYear()} Copyright:{" "}
          <a href="https://www.paintGuru.com" style={{ color: "yellow" }}>
            {" "}
            paintGuru.com{" "}
          </a>
        </MDBContainer>
      </div>
    </MDBFooter>
  );
};

export default FooterPage;
